.page__body {
  min-height: 100%;
  margin: 0;
  color: #000000;
  background-color: #e7e5e5;
  text-transform: uppercase;
}
.page__wrapper {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .page__wrapper {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
  }
}
.main-nav {
  position: relative;
  width: auto;
}
.main-nav__list {
  list-style: none;
  color: #ffffff;
  padding: 0;
  margin: 0;
  text-align: center;
  box-shadow: inset 0 -11px 5px -7px rgba(0, 0, 0, 0.2);
}
.main-nav__item {
  padding-top: 10px;
  padding-bottom: 5px;
  background-color: #81b3d2;
}
.main-nav__link {
  line-height: 40px;
}
@media (max-width: 767px) {
  .main-nav__link {
    color: #ffffff;
  }
  .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .main-nav__item:hover {
    background-color: #669ec0;
  }
  .main-nav__item:active {
    background-color: #5496bd;
  }
  .main-nav--opened {
    background: #ffffff;
    padding-bottom: 56px;
  }
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--opened .main-nav__item {
    display: block;
  }
  .main-nav__toggle {
    position: absolute;
    top: 0;
    right: 4px;
    display: block;
    width: 56px;
    height: 56px;
    background-color: transparent;
    cursor: pointer;
    border: none;
    padding: 0;
  }
  .main-nav__toggle:hover {
    opacity: 0.6;
  }
  .main-nav__toggle:active {
    opacity: 0.3;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: calc(50% - 2px);
    left: calc(50% - 12px);
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 -9px 0 0 #81b3d2, 0 9px 0 0 #81b3d2;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: calc(50% - 2px);
    left: calc(50% - 14px);
    width: 28px;
    height: 4px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav__item:not(:first-child) {
    box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__item {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  .main-nav--nojs {
    padding-bottom: 56px;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    color: #000000;
  }
  .main-nav__item {
    width: 20%;
    padding-top: 16px;
    padding-bottom: 14px;
    background-color: #ffffff;
  }
  .main-nav__item:nth-child(2) {
    padding-right: 20%;
  }
  .main-nav__link {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
    display: block;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
  .main-nav__link:active {
    color: #766357;
  }
  .main-nav__link:disabled {
    color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__item--current {
    color: #604e43;
  }
}
@media (min-width: 1200px) {
  .main-nav__item:nth-child(2) {
    padding-right: 20%;
  }
  .main-nav__item {
    width: 20%;
  }
}
