.review {
  padding: 0 20px;
}

.review__item {
  border: none;
  margin: 0;
  padding: 0;
}

.review label {
  color: @color-dark;
}

.review__title {
  font-size: 18px;
  line-height: 26px;
  padding: 0;
  padding-top: 20px;
  margin: 0 auto;
  text-align: center;
}

.review__text {
  outline: none;
}

@media (min-width: @tablet-width) {
  .review {
    padding: 0 60px;
  }

  .review__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 50px;
  }

  .review__title {
    font-size: 21px;
    margin: 0;
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .review {
    padding: 0 120px;
  }
}
